          <div id="twoButton">
            <el-button @click="changePWVisible = false">取 消</el-button>
            <el-button type="primary" @click="changePassword">确 定</el-button>
          </div>
        </div>
      </el-dialog>
    </div>
      </div>
      <!--<div class="jumpRouterDetail">
        论坛
      </div>
      <div class="jumpRouterDetail">
        广场
      </div>-->
    </div>
