  justify-content: space-between;
}

#jumpRouterPlace {
  display: inline-flex;
  margin-top: auto;
  margin-bottom: auto;
  height: 60px;
  align-items: center;
  padding-left: 15px;
  padding-right: 15px;
}

.jumpRouterDetail {
  font-size: 14px;
  color: #848484;
  letter-spacing: 3px;
  margin-right: 30px;
  height: 40px;
  padding-top: 20px;
  -ms-flex-direction: column;
  -ms-flex-align: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
}

.jumpRouterDetail:hover {
  font-size: 14px;
  color: #424242;
  letter-spacing: 3px;
  margin-right: 30px;
  cursor: pointer;
}

.myActiveClass {
  color: #FFFFFF;
  font-weight: bold;
}

.myLine {
  color: black;
  height: 3px;

}

#searchBar {
  margin-top: auto;
  margin-bottom: auto;
  width: 400px;
}

#searchWrapper {
  margin: auto;
  color: black;
}

#mySearchInput {
  width: 300px;
  height: 20px;
}

#otherWrapper {
  width: 400px;
  font-size: 20px;
  margin-top: 18px;
  display: inline-flex;

}

#infoBar {
  margin-top: auto;
  margin-bottom: auto;
  height: auto;
  width: auto;
}

.infoBox {
  margin-left: 20px;
  height: auto;
  width: auto;
  color: #FFF;
}

.infoBox:hover {
  color: #424242;
  cursor: pointer;
}

.myDropdown {
  border-left: transparent solid 2px;
}

.myDropdown:hover {
  background-color: whitesmoke;
  color: black;
}
.name{
  margin-left: 20px;
  margin-right: 20px;
  font-family: Georgia;
  color: #34898f;
  font-size: 25px;
}
#createNew {
  font-family: "Microsoft YaHei UI";
  display: inline;
  background-color: transparent;
  background-color: #34898f;
  font-family: "Roboto", Arial, sans-serif;
  color: #ffffff;
  font-size: 14px;
  cursor: pointer;
  border-radius: 3px;
  text-align: center;
  margin-left: 30px;
  padding: 10px;
  border: none;
}

#createNew:hover {
  background-color: #424242;
}

/deep/ .el-input__inner {
  -webkit-appearance: none;
  background-color: #FFF;
  background-image: none;
  /*border-radius: 4px;*/
  border: 1px solid #DCDFE6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 35px;
  /* width: 400px; */
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  /* -webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1); */
  transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
  width: 100%;
}

#Login {
  border: none;
  background: transparent;
  color: #6E6E6E;
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 15px;
  letter-spacing: 3px;
  padding: 5px 0px 5px 0px;
}

#Login:hover {
  cursor: pointer;
}

#register {
  border: none;
  background: transparent;
  color: #00CCBB;
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 15px;
  letter-spacing: 3px;
  margin-left: 20px;
  padding: 5px 0px 5px 0px;
}

#register:hover {
  cursor: pointer;
}
/deep/ .el-icon-arrow-down:before {
    content: "\E6DF";
    color: #FFF;
}
#alreadyLogin {
  margin-left: 15px;
  display: inline-flex;
}
</style>



